.coding-example-section {
    border-bottom: 1px solid black;
    display: flex;
    justify-content: space-between;
    margin-left: 30px;
    margin-right: 50px;
    padding-bottom: 25px;
}

@media only screen and (max-width: 600px) {
    .coding-example-section {
        flex-direction: column;
    }
}

.coding-example-link {
    margin-right: 15px;
}

.coding-example-desc {
    margin-right: 10px;
    width: 400px;
}

@media only screen and (max-width: 600px) {
    .coding-example-desc {
        width: 90%
    }
}

.coding-example-thumbnail {
    cursor: pointer;
    width: auto;
    margin-top: 30px;
}

.shorter {
    height: 300px;
}

.medium {
    height: 400px;
}

@media only screen and (max-width: 600px) {
    .coding-example-thumbnail {
        width: 90%;
        height: auto;
    }
}

